.Header {
    position: relative;
    height: 100%;
    border-right: 1px solid var(--color-gray);
    transition: var(--ms);
    width: 3.5rem;
}

.Header:hover {
    width: 14rem;
}

.Header ul {
    padding: 0;
    margin: 0;
    list-style: none;
}

.Header .hr {
    margin: auto;
    width: calc(100% - 1rem);
    height: 1px;
    background-color: var(--color-gray);
}

.Header li {
    cursor: pointer;
    margin: .5rem;
    width: calc(100% - 1rem);
    height: 2.5rem;
    border-radius: .25rem;
    display: flex;
    flex-direction: row;
    overflow: hidden;
    transition: var(--ms);
}

.Header li:not(:first-child):hover {
    background-color: var(--color-gray);
}

.Header li i {
    width: 2.5rem;
    height: 2.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    color: var(--color-black);
}

.Header li h6 {
    margin: 0;
    width: calc(100% - 2.5rem);
    line-height: 2.5rem;
    color: var(--color-black);
    transition: var(--ms);
    opacity: 0;
}

.Header:hover li h6 {
    opacity: 1;
}

.Header li:last-child {
    position: absolute;
    bottom: 0;
}