@import url(//fonts.googleapis.com/earlyaccess/notosanskr.css);

@font-face {
    font-family: 'JSArirang-Regular';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2001@1.1/JSArirang-RegularA1.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

:root {
    --color-black: rgb(50, 50, 50);
    --color-gray: rgb(240, 240, 240);
    --color-red: IndianRed;
    --color-green: DarkSeaGreen;
    --color-blue: CornflowerBlue;
    --ms: 500ms;
}

.noise {
    user-select: none;
    pointer-events: none;
    z-index: 100;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url('https://images.unsplash.com/photo-1534593963832-01c3595183bd?q=80&w=1335&auto=format&fit=crop&ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D');
    background-size: 1080px;
    filter: grayscale(0) contrast(.8);
    mix-blend-mode: multiply;
    transition: var(--ms);
    opacity: 0.2;
}

html,
body,
#root,
.App {
    margin: 0;
    width: 100%;
    height: 100%;
}

.App {
    display: flex;
    flex-direction: row;
}

main {
    position: relative;
    width: 100%;
    height: 100%;
}